@import "./theme/color/colors";
// Small tablets and large smartphones (landscape view)
$screen-sm-min: 576px;

// Small tablets (portrait view)
$screen-md-min: 768px;

// Tablets and small desktops
$screen-lg-min: 992px;

// Large tablets and desktops
$screen-xl-min: 1200px;

// Small devices
@mixin s-sm {
  @media (min-width: #{$screen-sm-min}) {
    @content;
  }
}

// Medium devices
@mixin s-md {
  @media (min-width: #{$screen-md-min}) {
    @content;
  }
}

// Large devices
@mixin s-lg {
  @media (min-width: #{$screen-lg-min}) {
    @content;
  }
}

// Extra large devices
@mixin s-xl {
  @media (min-width: #{$screen-xl-min}) {
    @content;
  }
}

:root {
  font-size: 13px;
}

@include s-md() {
  :root {
    font-size: 16px;
  }
}

$shadow-color: rgba(0, 0, 0, 0.1);

body {
  font-family: "Rubik", sans-serif;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

$colors: (
  "pale-grey": $pale-grey,
  "primary": $primary,
  "navy": $navy,
  "blue-grey": $blue-grey,
  "steel": $steel,
  "orange-yellow": $orange-yellow,
  "red": $red,
  "blue": $blue,
  "grey": $greyish-brown,
  "white": $white,
  "black": $black,
  "navBg": $black,
  "bg": $window-blue,
  "bg-5": $window-blue-5,
  "bg-10": $window-blue-10,
  "bg-17": $window-blue-17,
  "bg-grey": $gainsboro,
);

@each $name, $glyph in $colors {
  .color__#{$name} {
    color: $glyph;
  }

  .bg__#{$name} {
    background-color: $glyph;
  }
}

.bg__train{
  background-color: #ddfafe;
}

.text {
  font-stretch: normal;
  color: $greyish-brown;
  &__btn{
    font-size: 14px;
  }

  &__bold {
    font-weight: bold;
  }
  &__t1 {
    font-size: 3.3125rem;
  }

  &__h1 {
    font-size: 2.25rem;
  }

  &__h2 {
    font-size: 1.875rem;
  }

  &__h3 {
    font-size: 1.65rem;
  }

  &__h4 {
    font-size: 1.5rem;
  }

  &__quote {
    font-size: 1rem;
    font-style: italic;
  }

  &__b1 {
    font-size: 16px;
    line-height: 24px;
  }

  &__d1 {
    font-size: 1rem;
  }

  &__d2 {
    font-size: 1.75rem;
  }

  &__d3 {
    font-size: 0.75rem;
  }

  &__center{
    text-align: center;
  }
}

.font {
  &__bold {
    font-weight: bold;
  }

  &__normal{
    font-weight: normal;
  }

  &__medium{
    font-weight: medium;
  }
}

/* ------------- img ------------- */

.img {
  &__decorate {
    width: 500px;

    &--mobile-card {
      width: 480px;
    }

    &--side {
      width: 300px;
      height: 170px;
    }
  }

  &__logo {
    cursor: pointer;
    width: 150px;
    height: 52px;
  }

  &__icon {
    width: 32px;

    &--md {
      width: 64px;
      height: 64px;
    }
  }

  &__movie {
    &--poster {
      width: 320px;
      height: 30rem;
    }

    &--normal {
      width: 220px;
      height: 300px;
    }
  }

  &-sm {
    width: 150px;
    height: auto;
  }

  &-md {
    width: 250px;
    height: auto;
  }
}

.icon {
  width: 32px;
  height: 32px;

  &--tiny {
    width: 16px;
    height: 16px;
  }

  &--sm {
    width: 24px;
    height: 24px;
  }

  &--md {
    width: 64px;
    height: 64px;
  }
}

.anim {
  &--fadeIn {
    -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 2s; /* Firefox < 16 */
    -ms-animation: fadein 2s; /* Internet Explorer */
    -o-animation: fadein 2s; /* Opera < 12.1 */
    animation: fadein 2s;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-moz-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-ms-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

// ------------------------ shadow ------------------------

.a-shadow {
  box-shadow: 0 0 15px $shadow-color;
}

.footer {
  min-height: 20vh;
  background-color: transparent;
}

.menu-item {
  padding: 0 40px;
  margin: 5px 10px;
  user-select: none;
  cursor: pointer;
  border: none;
}
.menu-item-wrapper {
  border: none;
  outline: none;

  .active {
    border: none;
    outline: none;
  }
}
.menu-item.active {
  border: none;
  outline: none;
}

.scroll-menu-arrow {
  padding: 20px;
  cursor: pointer;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

.item-enter {
  opacity: 0;
}
.item-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.item-exit {
  opacity: 1;
}
.item-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}
